new Vue({
    el:'#app',
    delimiters: ['${','}'],
    data() {
        return {
            url:'',
            errors:[],
            app:'http://127.0.0.1:5000/',
            selected:'video',
            loading:false,
            done:-1,
            infos:{},
            options: [
                {text:'video',value:'video'},
                {text:'audio',value:'audio'},
                {text:'Video playlist',value:'playlist_video'},
                {text:'Audio playlist',value:'playlist_audio'}
            ]
        }
    },
    methods: {
        download : function () {
            var target = this.app+this.selected
            if (this.selected === 'video' || this.selected === 'audio' ){
                axios.post(this.app + 'get_infos', {url: this.url}).then(
                    (response) => {
                        this.infos = response.data
                        this.loading = this.infos.loading
                    }
                ).catch(e => {
                    this.errors.push(e.message);
                    console.log(e)
                })
            }else{
                axios.post(this.app + 'get_infos_playlist', {url: this.url}).then(
                    (response) => {
                        this.infos = response.data
                        this.loading = this.infos.loading

                    }
                ).catch(e => {
                    this.errors.push(e.message);
                    console.log(e)
                })
            }
            axios.post(target,{url:this.url}).then(
                () => {
                      this.loading = -1
                      this.done = 1
                      this.infos.video = 0
                }
            ).catch(e =>{ this.errors.push(e.message) ; console.log(e)})
                .finally(
                () => {
                    this.loading= -1
                    this.infos.video = 0
                }
            )
        }
    }
})